* {
  box-sizing: border-box; }

html {
  scroll-behavior: smooth; }

body {
  margin: 0 auto;
  padding: 0;
  background-color: #000000;
  color: #ffffff;
  font-family: "Raleway", "Arial", sans-serif; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.modal-overlay {
  overflow: hidden; }

.mobile-only {
  display: none; }

.tablet-only {
  display: none; }

.desktop-only {
  display: block; }

.main-screen {
  width: 100%;
  height: 100vh;
  position: relative; }

@media (max-width: 1023px) {
  .mobile-only {
    display: none; }
  .tablet-only {
    display: block; }
  .desktop-only {
    display: none; } }

@media (max-width: 767px) {
  .mobile-only {
    display: block; }
  .tablet-only {
    display: none; }
  .desktop-only {
    display: none; } }

.container {
  max-width: 1322px;
  padding: 0 10px;
  margin: 0 auto; }

@media (max-width: 1023px) {
  .container {
    padding: 0; } }

.page-header__content {
  display: flex;
  align-items: center;
  padding: 24px 0; }

.page-header__logo {
  margin-right: 8%; }

.page-header__contacts {
  display: flex;
  align-items: center;
  margin-left: 10.6%; }

.page-header__link {
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase; }

.page-header__city {
  color: #F2890F;
  display: flex;
  align-items: center;
  margin-right: 24px; }
  .page-header__city svg {
    margin-right: 8px; }

.main-nav {
  margin-left: auto;
  width: 40.8%; }

.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%; }

.main-nav__link {
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  font-size: 14px;
  line-height: 16px; }
